import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { NpcNewDTO } from 'src/app/models/Npc/npc_new.dto';
import { NpcDTO } from 'src/app/models/Npc/npc.dto';
import { StorageService } from '../storage-service.service';
import { NpcUpdateDTO } from 'src/app/models/Npc/npc-update.dto';
import { NpcBasicUpdateDTO } from 'src/app/models/Npc/npc-basic-update.dto';

@Injectable({
  providedIn: 'root'
})
export class NpcService {

  constructor(private http: HttpClient, private storage: StorageService) { }

  private staticID: string;

  public getStaticID(): string {
    return this.staticID;
  }

  public setStaticID(id: string): void {
    this.staticID = id;
  }

  insert(obj: NpcNewDTO): Observable<any> {
    return this.http.post(
      `${API_CONFIG.baseUrl}npc`, obj, {
      responseType: "json",
      observe: "body"
    });
  }

  update(obj: NpcDTO): Observable<any> {
    return this.http.post(
      `${API_CONFIG.baseUrl}npc`, obj, {
      responseType: "json",
      observe: "body"
    });
  }

  update2(obj: NpcUpdateDTO): Observable<any> {
    const response = this.http.put(
      `${API_CONFIG.baseUrl}npc/advanced`, obj, {
      responseType: "json",
      observe: "body"
    });
    return response;
  }

  updateBasic(obj: NpcBasicUpdateDTO): Observable<any> {
    const response = this.http.put(
      `${API_CONFIG.baseUrl}npc/basic`, obj, {
      responseType: "json",
      observe: "body"
    });
    return response;
  }

  findPageByCriador(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('idCriador', this.storage.getLocalUserId())
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}npc/criador`,
      {
        params,
        observe: 'body',
      });
  }

  findById(id: String): Observable<any> {
    return this.http.get(`${API_CONFIG.baseUrl}npc/${id}`,
      {
        responseType: "json",
        observe: 'body',
      });
  }
}
