import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { FichaModeloNew } from 'src/app/models/fichas/ficha-modelo-new.dto';
import { StorageService } from '../storage-service.service';

@Injectable({
  providedIn: 'root'
})
export class FichaModeloService {

  constructor(private http: HttpClient, private storage: StorageService) { }

  findFichaModeloById(id: string): Observable<any> {
    return this.http.get(`${API_CONFIG.baseUrl}fichamodelo/${id}`,
      {
        responseType: 'json',
        observe: "body"
      });
  }

  insert(dto: FichaModeloNew): Observable<any> {
    dto.idCriador = this.storage.getLocalUserId();
    return this.http.post(`${API_CONFIG.baseUrl}fichamodelo`, dto);
  }

  findPageByCriador(page: string = '0', linesPerPage: string = '10'): Observable<any> {
    const params = new HttpParams()
      .set('idCriador', this.storage.getLocalUserId())
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}fichamodelo/criador`,
      {
        params,
        observe: 'body',
      });
  }


}
