import { Component, OnInit, Input } from '@angular/core';
import { ModalController, AlertController } from '@ionic/angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ItemService } from 'src/app/services/criacoes/item/item.service';
import { StorageService } from 'src/app/services/storage-service.service';
import { NpcNewDTO } from 'src/app/models/Npc/npc_new.dto';
import { FichaModeloService } from 'src/app/services/fichas/ficha-modelo.service';
import { NpcService } from 'src/app/services/npc/npc.service';
import { ImagemComponent } from '../../imagem/imagem.component';
import { FichaService } from 'src/app/services/fichas/ficha.service';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';

@Component({
  selector: 'app-criar-npc',
  templateUrl: './criar-npc.component.html',
  styleUrls: ['./criar-npc.component.scss'],
})
export class CriarNpcComponent implements OnInit {

  paginaAtual = 0;

  formNpc: FormGroup;
  newNpc: NpcNewDTO;
  idFichaModelo = '';

  objetivos: string[] = [];
  objetivo: string = "";

  interesses: string[] = [];
  interesse: string = "";

  @Input('menu')
  menu: boolean = true;

  @Input('cancelarBTN')
  cancelarBTN: boolean = false;
  achado: boolean;

  loading: boolean = false;
  display: boolean = false;

  idCriado: string = '';

  constructor(public modalCtrl: ModalController,
    public formBuilder: FormBuilder,
    private fmService: FichaModeloService,
    private npcService: NpcService,
    public alertController: AlertController,
    public fichaService: FichaService,
    public storage: StorageService,
    private modalController: ModalController,
    private paginaService: PaginacaoService) {
    this.formNpc = this.formBuilder.group({
      historia: ['', [Validators.required]],
      valorInventario: ['', [Validators.required, Validators.pattern('[0-9]*')]],
      tipoNpc: ['', [Validators.required]],
    });
  }

  ngOnInit() {
    this.consultarFM();
  }

  salvar() {
    this.loading = true;
    this.newNpc = {
      historia: this.formNpc.get('historia').value,
      objetivos: this.objetivos,
      interesses: this.interesses,
      fichaNewDTO: {
        idFichaModelo: this.idFichaModelo,
        valorInventario: this.formNpc.get('valorInventario').value
      },
      tipoNpc: this.formNpc.get('tipoNpc').value,
      idCriador: this.storage.getLocalUserId()

    };

    this.npcService.insert(this.newNpc)
      .subscribe(res => {
        this.loading = false;
        this.idCriado = res.ficha.idFicha;
        this.fichaService.setConfiguration(res.ficha, true, res.idNpc, null, res.tipoNpc, false);
        this.showFicha();
        this.achado = false;
      },
        error => {
          this.loading = false;
        });
  }

  async showFicha() {
    this.display = true;
  }

  consultar() {
    this.loading = true;
    this.display = false;
    this.achado = false
    this.fmService.findFichaModeloById(this.idFichaModelo)
      .subscribe(res => {
        this.loading = false;
        this.achado = true
      },
        error => {
          this.loading = false;
          this.achado = false
        });
  }

  inserirObjetivo() {
    if (this.objetivo.length < 5) {
      this.presentError('objetivo');
    } else {
      this.objetivos.push(this.objetivo);
      this.objetivo = "";
    }
  }

  inserirInteresse() {
    if (this.interesse.length < 5) {
      this.presentError('interesse');
    } else {
      this.interesses.push(this.interesse);
      this.interesse = "";
    }

  }

  async presentError(texto: string) {
    const alert = await this.alertController.create({
      header: 'Valor inválido',
      message: 'Insira um ' + texto + ' com pelo menos 5 caracteres',
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary'
        }
      ]
    });

    await alert.present();
  }

  async img() {
    const modal = await this.modalController.create({
      component: ImagemComponent,
      backdropDismiss: false,
      cssClass: 'my-full',

    });

    return await modal.present();
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  consultarFM() {
    const p = this.fmService.findPageByCriador(this.paginaAtual.toString());
    this.paginaService.setPromisse(p);
  }

  async selectedObj(event) {
    //console.log(event);

    const { id } = event;
    this.idFichaModelo = id;
    this.consultar();

  }
}
